* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
.App {
  margin: 0 auto;

  width: 500px;
  margin-top: 50px;
  background-color: #e0d4d3;
  text-align: center;
  display: flex;
  justify-content: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
li {
  list-style: none;
}
.profile {
  width: 300px;
  background-color: #fff;
  border-radius: 8px;
  margin-top: 50px;
  margin-bottom: 50px;
  .avatar {
    margin-top: 40px;
    width: 100px;
    border-radius: 50%;
  }
}

.name {
  font-weight: bold;
  font-size: 20px;
}
.tag,
.location {
  margin-top: 15px;
  color: rgb(138, 122, 122);
  font-weight: normal;
  font-size: 18px;
}
.stats {
  margin-top: 40px;
  min-height: 40px;
  background-color: rgb(233, 221, 221);
  margin-left: 0;
  display: flex;
  justify-content: space-around;
}
.stats li {
  padding: 15px 0;
  width: 33.3%;
  border: 1px solid gray;
  /* display: flex;
  justify-content: center;
  align-items: center; */
  display: block;
}
.label {
  text-align: center;
  display: block;
}
.quantity {
  text-align: center;
  display: block;
}
